body { font-family: Inter, ui-sans-serif, system-ui; margin:0; padding:10px; }
.app header { margin-bottom:8px }
.controls textarea { width:100%; height:70px }
.controls button { margin-top:8px }
.layout { display:flex; gap:12px }
.kanban { display:flex; gap:8px; flex:1 }
.col { background:#f7f8fb; padding:8px; border-radius:8px; width:40% }
.task-card { background:white; padding:8px; margin:6px 0; border-radius:6px; box-shadow:0 2px 6px rgba(0,0,0,0.06) }
.agent-panel { background:#fff; padding:8px; border-radius:8px; margin-bottom:8px }
.console { background:#0b1220; color:#e6eef8; padding:8px; border-radius:8px; height:200px; overflow:auto }